import { Container } from "@mui/material";
import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import ModalCreateTeam from "../components/Teams/ModalCreateTeam";
import { useDispatch, useSelector } from "react-redux";
import TableTeam from "../components/Teams/TableTeam";
import {
    addTeam,
    cleanUpToast,
    getTeams,
    removeTeam,
} from "../features/teams/teamsSlice";
import AppBarResponsive from "../components/Teams/AppBarResponsive";
import { Toaster, toast } from "react-hot-toast";
const Home = () => {
    const teams = useSelector((state) => state.teams);
    const auth = useSelector((state) => state.auth);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    useEffect(() => {
        if (auth.logged === false) navigate("/workflow-front");
    }, [auth]);

    useEffect(() => {
        dispatch(getTeams());
    }, []);

    useEffect(() => {
        if (teams.addTeamStatus === "rejected") {
            toast.error(teams.addTeamError);
        }

        /* setTimeout(() => {
            dispatch(cleanUpToast());
        }, 3000); */
    }, [teams]);

    const createTeam = (team) => {
        dispatch(addTeam(team));
    };

    const deleteTeam = (idTeam) => {
        dispatch(removeTeam(idTeam));
    };
    return (
        <div className="w-full h-screen">
            <AppBarResponsive />
            <Container sx={{ marginY: 5 }}>
                <div className="flex justify-between mb-4 items-center">
                    {/* My Workspace */}
                    <p className="text-3xl font-semibold md:text-3xl text-center">
                        {/* ya no sera equipos y mis equipos para los demas que no son leader */}
                        {auth.user.role === 3000 ? "Tableros" : "Mi Tablero"}
                    </p>
                    {auth.user.role === 3000 && (
                        <ModalCreateTeam
                            createTeam={createTeam}
                            /*  updateData={updateData}
                        dataToEdit={dataToEdit}
                        setDataToEdit={setDataToEdit} */
                        />
                    )}
                </div>
                {teams.teams && (
                    <TableTeam
                        teams={teams.teams}
                        // setDataToEdit={setDataToEdit}
                        deleteTeam={deleteTeam}
                    />
                )}
            </Container>
            <Toaster />
        </div>
    );
};

export default Home;
