import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { Link, useNavigate } from "react-router-dom";
import AddIcon from "@mui/icons-material/Add";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { signUp } from "../features/auth/authSlice";
import { Toaster, toast } from "react-hot-toast";
import { cleanup } from "@testing-library/react";
import Providers from "../components/SignUp/Providers";

export default function SignInSide() {
    const APIREST = process.env.REACT_APP_APIREST;
    const auth = useSelector((state) => state.auth);
    const theme = createTheme();
    const dispatch = useDispatch();
    const navigate = useNavigate();
    /* console.log(auth.logged); */
    useEffect(() => {
        if (auth.logged) navigate("/teams");
    }, [auth]);

    useEffect(() => {
        /* Esto tendra que limpiarlo no?, es decir, limpiar el signUpStatus = "rejected" a vacio ya que si voy a login y vuelvo a register me saldra nuevamente el error */
        if (auth.statusSignUp === "rejected") {
            toast.error(auth.messageSignUp);
        }
    }, [auth]);

    /* Otra forma de registrarse sera hacer todo en el evento sign in / submit, es decir, agarrar recien los datos ya que probablemente son los finales y crearlo, pero la ventaja de que esten controlados es que puedo hacer validaciones */
    const initialState = {
        name: "",
        email: "",
        password: "",
        img: null,
    };

    const [credentials, setCredentials] = useState(initialState);
    const handleSubmit = () => {
        const user = new FormData();
        user.append("name", credentials.name);
        user.append("email", credentials.email);
        user.append("password", credentials.password);
        user.append("img", credentials.img);
        dispatch(signUp(user));
    };

    const handleChange = (e) => {
        setCredentials({
            ...credentials,
            [e.target.name]: e.target.value,
        });
    };

    const handleChangeFile = (img) => {
        setCredentials({
            ...credentials,
            img: img,
        });
    };

    return (
        <ThemeProvider theme={theme}>
            <Grid container component="main" sx={{ height: "100vh" }}>
                <CssBaseline />
                <Grid
                    item
                    xs={false}
                    sm={4}
                    md={7}
                    className="bg-auth bg-no-repeat bg-cover bg-center"
                />
                <Grid
                    item
                    xs={12}
                    sm={8}
                    md={5}
                    component={Paper}
                    elevation={6}
                    square
                >
                    <Box className="flex flex-col items-center my-8 px-8  lg:px-10">
                        {/* Sign Up */}
                        <Typography component="h1" variant="h4">
                            Registrate
                        </Typography>
                        <Toaster />
                        <Box noValidate sx={{ mt: 1 }}>
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                id="name"
                                label="Nombre"
                                name="name"
                                autoComplete="name"
                                autoFocus
                                value={credentials.name}
                                onChange={handleChange}
                            />
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                id="email"
                                label="Correo"
                                name="email"
                                autoComplete="email"
                                value={credentials.email}
                                onChange={handleChange}
                            />
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                name="password"
                                label="Contrasea"
                                type="password"
                                id="password"
                                autoComplete="current-password"
                                value={credentials.password}
                                onChange={handleChange}
                            />
                            {/* No abarca todo el boton, solamente cuando aparece la manito */}
                            <Button
                                fullWidth
                                variant="outlined"
                                sx={{ cursor: "default", marginTop: 2 }}
                            >
                                <label
                                    htmlFor="img"
                                    className="file w-full cursor-pointer"
                                >
                                    <input
                                        className="hidden "
                                        id="img"
                                        name="img"
                                        type="file"
                                        onChange={(e) =>
                                            handleChangeFile(e.target.files[0])
                                        }
                                        /* accept=".png,.jpeg,.jpg" */
                                    />
                                    {/* Profile Picture */}
                                    <AddIcon /> Foto de Perfil
                                </label>
                            </Button>
                            <Button
                                fullWidth
                                variant="contained"
                                sx={{ mt: 3, mb: 1 }}
                                onClick={handleSubmit}
                            >
                                Registrarse
                            </Button>
                            {/* <Providers /> */}
                            <Typography
                                component="h6"
                                variant="h6"
                                color="primary"
                            >
                                {/* Already have an account Login Now */}
                                <Link to={"/workflow-front"}>
                                    <p className="text-[18px] mt-2">
                                        Ya tienes una cuenta, Inicia Sesin
                                    </p>
                                </Link>
                            </Typography>
                        </Box>
                    </Box>
                </Grid>
            </Grid>
        </ThemeProvider>
    );
}
