import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { aDelete, aGet, aPost, aPut } from "../../axios";

const initialState = {
    teams: [],
    /* dependiendo de si fue fulfilled o rejected, mostrara un mensaje */
    getTeamsStatus: "",
    getTeamsError: "",
    addTeamStatus: "",
    addTeamError: "",
    updateTeamStatus: "",
    updateTeamError: "",
    deleteTeamStatus: "",
    deleteTeamError: "",
};

/* ****************************** Teams ****************************** */

export const getTeams = createAsyncThunk(
    "teams/getTeams",
    async (noData, { rejectWithValue }) => {
        try {
            const response = await aGet("/teams");
            console.log(`que trae ac:`, response.data);
            return response.data;
        } catch (error) {
            console.log(error.response.data);
            /* mandamos como action.payload el error que nos retorna el backend */
            return rejectWithValue(error.response?.data);
        }
    }
);

export const addTeam = createAsyncThunk(
    "teams/addTeam",
    async (team, { rejectWithValue }) => {
        try {
            const response = await aPost("/teams", team);
            console.log(`que trae ac en createTeams:`, response.data);
            return response.data;
        } catch (error) {
            console.log(error.response.data);
            return rejectWithValue(error.response?.data);
        }
    }
);

/* Aun no implementado */
export const updateTeam = createAsyncThunk(
    "teams/updateTeam",
    async (idTeam, { rejectWithValue }) => {
        try {
            const response = await aPut("/teams/" + idTeam);
            return response.data;
        } catch (error) {
            return rejectWithValue(error.response?.data);
        }
    }
);

export const removeTeam = createAsyncThunk(
    "teams/removeTeam",
    async (idTeam, { rejectWithValue }) => {
        try {
            const response = await aDelete("/teams/" + idTeam);
            return response.data;
        } catch (error) {
            return rejectWithValue(error.response?.data);
        }
    }
);

const teamsSlice = createSlice({
    name: "teams",
    initialState,
    reducers: {
        cleanUpToast(state, action) {
            state.getTeamsStatus = "";
            state.getTeamsError = "";
            state.addTeamStatus = "";
            state.addTeamError = "";
            state.updateTeamStatus = "";
            state.updateTeamError = "";
            state.deleteTeamStatus = "";
            state.deleteTeamError = "";
        },
    },
    extraReducers: {
        [getTeams.pending]: (state, action) => {
            return {
                ...state,
                teams: [],
                getTeamsStatus: "pending",
            };
        },
        [getTeams.fulfilled]: (state, action) => {
            return {
                ...state,
                teams: action.payload,
                getTeamsStatus: "success",
            };
        },
        [getTeams.rejected]: (state, action) => {
            return {
                /* no se como podra ocurrir algo as xd */
                ...state,
                teams: [],
                getTeamsStatus: "rejected",
            };
        },
        [addTeam.pending]: (state, action) => {
            return {
                ...state,
                addTeamStatus: "",
            };
        },
        [addTeam.fulfilled]: (state, action) => {
            return {
                ...state,
                teams: [...state.teams, action.payload.team],
                /* addTeamStatus: "success", */
            };
        },
        [addTeam.rejected]: (state, action) => {
            return {
                ...state,
                addTeamStatus: "rejected",
                addTeamError: action.payload.message,
            };
        },
        [removeTeam.pending]: (state, action) => {
            return {
                ...state,
                getTeamsStatus: "",
                getTeamsError: "",
                addTeamStatus: "",
                addTeamError: "",
                updateTeamStatus: "",
                updateTeamError: "",
                deleteTeamStatus: "pending",
                deleteTeamError: "",
            };
        },
        [removeTeam.fulfilled]: (state, action) => {
            const currentTeams = state.teams.filter(
                (team) => team._id !== action.payload._id
            );
            return {
                ...state,
                teams: currentTeams,
                getTeamsStatus: "",
                getTeamsError: "",
                addTeamStatus: "",
                addTeamError: "",
                updateTeamStatus: "",
                updateTeamError: "",
                deleteTeamStatus: "success",
                deleteTeamError: "",
            };
        },
        [removeTeam.rejected]: (state, action) => {
            state = {
                ...state,
                getTeamsStatus: "",
                getTeamsError: "",
                addTeamStatus: "",
                addTeamError: "",
                updateTeamStatus: "",
                updateTeamError: "",
                deleteTeamStatus: "rejected",
                deleteTeamError: action.payload.message,
            };
        },
    },
});

export const { cleanUpToast } = teamsSlice.actions; // Esto se utiliza en el dispatch
export default teamsSlice.reducer; // Esto en el store
