import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import { Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { cleanUpToast, login } from "../features/auth/authSlice";
import { useEffect } from "react";
import { Toaster, toast } from "react-hot-toast";
import Providers from "../components/SignUp/Providers";

export default function SignInSide() {
    const theme = createTheme();
    const auth = useSelector((state) => state.auth);
    const dispatch = useDispatch();
    const navigate = useNavigate();

    useEffect(() => {
        if (auth.logged) navigate("/teams");
    }, [auth]);

    /* Esto tendra que limpiarlo no?, es decir, limpiar el signUpStatus = "rejected" a vacio ya que si voy a login y vuelvo a register me saldra nuevamente el error */
    useEffect(() => {
        if (auth.statusLogin === "rejected") {
            toast.error(auth.messageLogin);
        }
    }, [auth]);

    const initialState = {
        email: "",
        password: "",
    };

    const [credentials, setCredentials] = useState(initialState);

    const handleSubmit = () => {
        dispatch(login(credentials));
    };

    const handleChange = (e) => {
        setCredentials({
            ...credentials,
            [e.target.name]: e.target.value,
        });
    };

    const leaderAccount = () => {
        setCredentials({
            ...credentials,
            email: "leader@gmail.com",
            password: "leader123",
        });
    };
    const editorAccount = () => {
        setCredentials({
            ...credentials,
            email: "editor@gmail.com",
            password: "editor123",
        });
    };
    const normalAccount = () => {
        setCredentials({
            ...credentials,
            email: "normal@gmail.com",
            password: "normal123",
        });
    };

    return (
        <ThemeProvider theme={theme}>
            <Grid container component="main" sx={{ height: "100vh" }}>
                <CssBaseline />
                <Grid
                    item
                    xs={false}
                    sm={4}
                    md={7}
                    className="bg-auth bg-no-repeat bg-cover bg-center"
                />
                <Grid
                    item
                    xs={12}
                    sm={8}
                    md={5}
                    component={Paper}
                    elevation={6}
                    square
                >
                    <Box className="flex flex-col items-center my-8 px-8 lg:px-10 ">
                        {/* Login */}
                        <Typography component="h1" variant="h4">
                            Iniciar Sesin
                        </Typography>
                        <Toaster />
                        <Box component="div" noValidate sx={{ mt: 1 }}>
                            <div className="flex gap-2 lg:justify-between my-4">
                                <Button
                                    variant="contained"
                                    endIcon={<AccountCircleIcon />}
                                    onClick={leaderAccount}
                                >
                                    Leader
                                </Button>
                                <Button
                                    variant="contained"
                                    endIcon={<AccountCircleIcon />}
                                    onClick={editorAccount}
                                >
                                    Editor
                                </Button>
                                <Button
                                    variant="contained"
                                    endIcon={<AccountCircleIcon />}
                                    onClick={normalAccount}
                                >
                                    Normal
                                </Button>
                            </div>
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                id="email"
                                /* Email Address */
                                label="Correo"
                                name="email"
                                autoComplete="email"
                                autoFocus
                                value={credentials.email}
                                onChange={handleChange}
                            />
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                id="password"
                                label="Contrasea"
                                name="password"
                                type="password"
                                autoComplete="current-password"
                                value={credentials.password}
                                onChange={handleChange}
                            />
                            {/* Login */}
                            <Button
                                onClick={handleSubmit}
                                fullWidth
                                variant="contained"
                                sx={{ mt: 3, mb: 2 }}
                            >
                                Iniciar Sesin
                            </Button>
                            {/* <Providers /> */}
                            <Typography
                                component="h6"
                                variant="h6"
                                color="primary"
                            >
                                {/* Dont have an account? Sign Up */}
                                <Link
                                    to={"/signup"}
                                    style={{ textDecoration: "none" }}
                                >
                                    No tienes una cuenta? Registrate
                                </Link>
                            </Typography>
                        </Box>
                    </Box>
                </Grid>
            </Grid>
        </ThemeProvider>
    );
}
