import * as React from "react";
import Backdrop from "@mui/material/Backdrop";
import Modal from "@mui/material/Modal";
import Fade from "@mui/material/Fade";
import Button from "@mui/material/Button";
import CreateTeam from "./CreateTeam";
import GroupsIcon from "@mui/icons-material/Groups";

export default function TransitionsModal({ createTeam }) {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    return (
        <div>
            <Button
                variant="contained"
                endIcon={<GroupsIcon />}
                onClick={handleOpen}
                className="w-[18  0px] flex items-center"
            >
                {/* Agregar Equipo */}
                Agregar Tablero
            </Button>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    <div className="absolute top-[50%] left-[50%] -translate-x-2/4 -translate-y-2/4 lg:w-[490px] md:w-[420px] w-[320px] bg-[white] shadow-2xl rounded-[3px] border-none outline-none">
                        <CreateTeam
                            createTeam={createTeam}
                            handleClose={handleClose}
                        />
                    </div>
                </Fade>
            </Modal>
        </div>
    );
}
